body {
  background: #2e2e2e !important;
}

.wrapper {
  margin-top: 80px;
  margin-bottom: 80px;
}

.form-signin {
  max-width: 380px;
  padding: 15px 35px 45px;
  margin: 0 auto;
  background-color: black;
  border: 1px solid rgba(0, 0, 0, 0.1);


  .form-signin-heading,
  .checkbox {
    margin-bottom: 30px;
  }
  .error-message{
    color: white;
  }

  .checkbox {
    font-weight: normal;
  }

  .form-control {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    width: 350px;
    @include box-sizing(border-box);

    &:focus {
      z-index: 2;
    }
  }

  .forgetpassword {
    margin-top: 10px;
    text-align: center; /* Center the text */
  }

  input[type="text"] {
    margin-bottom: 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  input[type="password"] {
    margin-bottom: 20px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  .btn {
    width: 380px;
    height: 35px;
    border-radius: 5px;
    border-color: #38ab5f;
    background-color: #38ab5f;
    color: white;
  }
}

.inner-wrapper{
  background-color: #2e2e2e;
  height: 600px;
}


.image{
  height: 150px;
  width: 300px;
  margin-left: 40px;
  margin-top: 40px;
  margin-bottom: 40px;
  background-image: url(./1.png);
}